import React from 'react'

// 3rd party libs
import styled from "styled-components"

const Item = styled.div`
justify-self: center;
align-self: center;
`

const Header = styled.div`
font-size: 30px;
margin: 0;
color: #f49b42;
font-family: "Anton";
/*   -webkit-text-stroke: 2px black; */
text-shadow: 1px 1px 0px black, 2px 2px 0px black;
/*   transform: skew(-5deg) rotate(-3deg); */
`

const Content = styled.div`
text-shadow: 1px 1px 0px black, 2px 2px 0px black;
color: white;
font-size: 40px;
text-align: center;
font-family: "Anton";
`

const calculateContent = (baseValue, divider) => {
  console.log(baseValue, divider)
  return Math.floor(baseValue) / divider
}


const FunFactItem = (props) => {
  const suffix = props.suffix ? props.suffix : ""

  console.log(calculateContent(props.baseValue, props.divider))
  return (
    <Item>
      <Header>{props.header}</Header>
      <Content>{calculateContent(props.baseValue, props.divider)} {suffix}</Content>
    </Item>
  )
}

export default FunFactItem
